.post-card-tag {
    background-color: #f5f5f5;
    color: #666666;
    border-radius: 4px;
    padding: 4px 12px;
    margin-right: 8px;
    display: inline-block;
    font-size: 14px;
    line-height: 1.5;
    transition: all 0.2s ease-in-out;
  }
  
  .post-card-tag a {
    color: inherit;
    text-decoration: none;
  }
  
  .post-card-tag:hover {
    background-color: #007fff;
    color: #ffffff;
    cursor: pointer;
  }